body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
body {
  background-color: cadetblue;
}

img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.img__box {
  display: grid;
  width: 10px;
  height: 10px;
  cursor: pointer;
  border: solid 1px;
  padding: 3px;
  border-radius: 5px;
  background-color: aliceblue;
}
.img__box:hover {
  background-color: bisque;
}

.item__img {
  border: solid 1px;
  min-width: 100px;
  width: 500px;
  min-height: 300px;
  height: 710px;
  background-color: aliceblue;
  padding: 5px;
  margin: 20px;
  border-radius: 5px;
  position: sticky;
}

.panel_3 {
  display: grid;
  width: auto;
  grid-gap: 10px;
  margin-top: 20px;
  right: 20px;
  z-index: 1;
}

.box_ui {
  display: grid;
  grid-template-columns: auto auto 1fr;
}
.box_panel {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  align-items: flex-start;
}
ul {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 15px;
  margin-left: 0;
  padding-left: 0;
}
ul li {
  list-style-type: none;
  padding: 5px;
  border-radius: 5px;
  border: solid aliceblue;
}
ul li:hover {
  border: solid #ed7d31;
}
ul li div {
  background-repeat: no-repeat;
  background-size: cover;
  width: 140px;
  height: 200px;
  background-color: #4472c4;
  border-radius: 5px;
}
ul li:hover div {
  background-color: #ed7d31;
}

