.modal {
  position: absolute;
  background-color: aliceblue;
  padding: 20px;
  border-radius: 5px;
  /* display: flex; */
  flex-direction: column;
  align-items: center;
  display: none;
}
.modalActiv {
  position: absolute;
  top: 0;
    z-index: 1;
  background-color: aliceblue;
  padding: 20px;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  display: flex;
}